/**********************************************
          Controls
***********************************************/

.header {
  width: 100%;
  height: 200px;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  animation-duration: 1s;
  animation-name: header-slide-open;
}

@keyframes header-slide-open {
  0% {
    margin-top: -300px;
  }
  100% {
    margin-top: 0px;
  }
}

.header-container {
  width: 98%;
  height: 92%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 16px black;
}

.header-container-background  {
  width: 100%;
  height: 100%;
  background: #dce4e7;
  opacity: .9;
  position: absolute;
  z-index: -1;
}

.controls-header  {
  width: 60%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  animation:fadeIn ease-in 1;
  animation-fill-mode:forwards;
  animation-duration:1s;
  animation-delay: 0s;
}

@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
@-moz-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
@keyframes fadeIn { from { opacity:0; } to { opacity:1; } }

.title-container  {
  width: 100%;
  height: 60%;
  background: #1b1f2a;
  box-shadow: 0px 0px 3px black;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 60px;
  height: 60px;
  margin-right: 10px;
  animation:fadeIn ease-in 1;
  animation-fill-mode:forwards;
  animation-duration:3s;
  animation-delay: 0s;
}

.title{
  font-size: 370%;
  color: #FFF;
  animation-duration: 2s;
  animation-name: title-grow;
}

@keyframes title-grow {
  0% {
    font-size: 0%;
  }
  100% {
    font-size: 370%;
  }
}

.input-container  {
  width: 99%;
  height: 40%;
  font-size: 20px;
  background: #dce4e7;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 0px 0px 3px black;
  display: flex;
  justify-content: center;
  align-items: center;
  animation-duration: 3s;
  animation-name: input-slide-open;
}

@keyframes input-slide-open {
  0% {
    height: 0px;
  }
  100% {
    height: 40%;
  }
}

.input-box  {
  width: 80%;
  height: 50%;
  background: #dce4e7;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  box-shadow: 0px 0px 3px black;
  align-items: center;
  display: flex;
  justify-content: center;
  animation:fadeIn ease-in 1;
  animation-fill-mode:forwards;
  animation-duration:5s;
  animation-delay: 0s;
}

.search-input {
  width: 70%;
  height: 100%;
  font-size: 20px;
}

.reset-btn {
  width: 30%;
 height: 98%;
 background: #1b1f2a;
 align-items: center;
 display: flex;
 justify-content: center;
 border-top-right-radius: 3px;
 border-bottom-right-radius: 3px;
 box-shadow: 0px 0px 3px black;
}

.reset-btn:hover {
  cursor: pointer;
  background: #619ddb;
}

.reset-btn-title {
  font-size: 20px;
  color: #FFF;
}


/***************************************
media query 1000
****************************************/

@media screen and (max-width: 1000px) {

  .title {
    font-size: 275%;
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 275%;
    }
  }

  .logo{
    width: 40px;
    height: 40px;
  }

}


/***************************************
media query 750
****************************************/

@media screen and (max-width: 750px) {


  .title {
    font-size: 185%;
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 185%;
    }
  }

  .logo{
    width: 30px;
    height: 30px;
  }

  .reset-btn-title {
    font-size: 15px;
  }

  .title-container  {
    height: 40%;
  }

  .search-input  {
    font-size: 15px;
  }

}


/***************************************
media query 510
****************************************/

@media screen and (max-width: 510px) {


  .title {
    font-size: 164%;
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 164%;
    }
  }

  .logo{
    width: 30px;
    height: 30px;
  }

  .controls-header  {
    width: 94%;
  }

  .header-container {
    height: 70%;
  }

  .header {
    height: 188px;
    align-items: baseline;
  }

  .top-placeholder  {
    height: 140px;
  }

  .input-box  {
    width: 92%;
    height: 68%;
  }

}
