
/**********************************************
          General
***********************************************/

* {
  box-sizing: border-box;
  margin: 0px;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/*APP COMPONENT RELATED CSS GOES HERE*/

.false {
  width: 280px;
  height: 300px;
  margin: 8px 8px 8px 8px;
  border-radius: 8px;
  box-shadow: 0px 0px 16px black;
}

.false:hover  {
  box-shadow: 0px 0px 30px #619ddb;
  cursor: pointer;
}

.card:hover .card-title-container {
  background: #619ddb;
}

.card-data {
  width: 100%;
  height: 260px;
  background: #dce4e7;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.data {
  font-size: 16px;
  margin-bottom: 2px;
}

.card-title-container {
  width: 100%;
  height: 40px;
  background: #1b1f2a;
  align-items: center;
  display: flex;
  justify-content: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.card-title {
  font-size: 14px;
  color: #FFF;
}

.true  {
  width: 280px;
  height: 300px;
  margin: 8px 8px 8px 8px;
  border-radius: 8px;
  box-shadow: 0px 0px 16px red;
}

.true:hover  {
  cursor: pointer;
}

/**********************************************
          Card Display
***********************************************/

.card-display {
  width: 100%;
}

@-webkit-keyframes card-display-slide-open {
  0% {
    margin-top: -2000px;
  }
  100% {
    margin-top: 0px;
  }
}

@keyframes card-display-slide-open {
  0% {
    margin-top: -2000px;
  }
  100% {
    margin-top: 0px;
  }
}

.top-placeholder  {
  width: 100%;
  height: 220px;
}

.card-container {
  width: 98%;
  height: 70vh;
  background: #FFF;
  opacity: .95;
  border-radius: 10px;
  box-shadow: 0px 0px 16px black;
  margin: auto;
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: scroll;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-name: card-display-slide-open;
          animation-name: card-display-slide-open;
}

/**********************************************
          Controls
***********************************************/

.header {
  width: 100%;
  height: 200px;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-name: header-slide-open;
          animation-name: header-slide-open;
}

@-webkit-keyframes header-slide-open {
  0% {
    margin-top: -300px;
  }
  100% {
    margin-top: 0px;
  }
}

@keyframes header-slide-open {
  0% {
    margin-top: -300px;
  }
  100% {
    margin-top: 0px;
  }
}

.header-container {
  width: 98%;
  height: 92%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 0px 16px black;
}

.header-container-background  {
  width: 100%;
  height: 100%;
  background: #dce4e7;
  opacity: .9;
  position: absolute;
  z-index: -1;
}

.controls-header  {
  width: 60%;
  height: 90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  -webkit-animation:fadeIn ease-in 1;
          animation:fadeIn ease-in 1;
  -webkit-animation-fill-mode:forwards;
          animation-fill-mode:forwards;
  -webkit-animation-duration:1s;
          animation-duration:1s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:1; } }
@keyframes fadeIn { from { opacity:0; } to { opacity:1; } }

.title-container  {
  width: 100%;
  height: 60%;
  background: #1b1f2a;
  box-shadow: 0px 0px 3px black;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo {
  width: 60px;
  height: 60px;
  margin-right: 10px;
  -webkit-animation:fadeIn ease-in 1;
          animation:fadeIn ease-in 1;
  -webkit-animation-fill-mode:forwards;
          animation-fill-mode:forwards;
  -webkit-animation-duration:3s;
          animation-duration:3s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.title{
  font-size: 370%;
  color: #FFF;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: title-grow;
          animation-name: title-grow;
}

@-webkit-keyframes title-grow {
  0% {
    font-size: 0%;
  }
  100% {
    font-size: 370%;
  }
}

@keyframes title-grow {
  0% {
    font-size: 0%;
  }
  100% {
    font-size: 370%;
  }
}

.input-container  {
  width: 99%;
  height: 40%;
  font-size: 20px;
  background: #dce4e7;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 0px 0px 3px black;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-name: input-slide-open;
          animation-name: input-slide-open;
}

@-webkit-keyframes input-slide-open {
  0% {
    height: 0px;
  }
  100% {
    height: 40%;
  }
}

@keyframes input-slide-open {
  0% {
    height: 0px;
  }
  100% {
    height: 40%;
  }
}

.input-box  {
  width: 80%;
  height: 50%;
  background: #dce4e7;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  box-shadow: 0px 0px 3px black;
  align-items: center;
  display: flex;
  justify-content: center;
  -webkit-animation:fadeIn ease-in 1;
          animation:fadeIn ease-in 1;
  -webkit-animation-fill-mode:forwards;
          animation-fill-mode:forwards;
  -webkit-animation-duration:5s;
          animation-duration:5s;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.search-input {
  width: 70%;
  height: 100%;
  font-size: 20px;
}

.reset-btn {
  width: 30%;
 height: 98%;
 background: #1b1f2a;
 align-items: center;
 display: flex;
 justify-content: center;
 border-top-right-radius: 3px;
 border-bottom-right-radius: 3px;
 box-shadow: 0px 0px 3px black;
}

.reset-btn:hover {
  cursor: pointer;
  background: #619ddb;
}

.reset-btn-title {
  font-size: 20px;
  color: #FFF;
}


/***************************************
media query 1000
****************************************/

@media screen and (max-width: 1000px) {

  .title {
    font-size: 275%;
  }

  @-webkit-keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 275%;
    }
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 275%;
    }
  }

  .logo{
    width: 40px;
    height: 40px;
  }

}


/***************************************
media query 750
****************************************/

@media screen and (max-width: 750px) {


  .title {
    font-size: 185%;
  }

  @-webkit-keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 185%;
    }
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 185%;
    }
  }

  .logo{
    width: 30px;
    height: 30px;
  }

  .reset-btn-title {
    font-size: 15px;
  }

  .title-container  {
    height: 40%;
  }

  .search-input  {
    font-size: 15px;
  }

}


/***************************************
media query 510
****************************************/

@media screen and (max-width: 510px) {


  .title {
    font-size: 164%;
  }

  @-webkit-keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 164%;
    }
  }

  @keyframes title-grow {
    0% {
      font-size: 0%;
    }
    100% {
      font-size: 164%;
    }
  }

  .logo{
    width: 30px;
    height: 30px;
  }

  .controls-header  {
    width: 94%;
  }

  .header-container {
    height: 70%;
  }

  .header {
    height: 188px;
    align-items: baseline;
  }

  .top-placeholder  {
    height: 140px;
  }

  .input-box  {
    width: 92%;
    height: 68%;
  }

}

/**********************************************
          Controls
***********************************************/

.background-container  {
  width: 100%;
  height: 100vh;
  position: absolute;
  overflow: hidden;
  z-index: -1000;
}

.background-graphic  {
  width: 100%;
  height: 100vh;
  position: absolute;
  object-fit: cover;
}


/**********************************************
          Compare Display
***********************************************/

.compare-overlay  {
  width: 100%;
  height: 100vh;
  position: absolute;
  background: #FFF;
  opacity: .8;
  top: 0;
  left: 0;
  z-index: 999;
  -webkit-animation-name: compare-overlay-slide-open;
          animation-name: compare-overlay-slide-open;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

@-webkit-keyframes compare-overlay-slide-open {
  0% {
    top: -2000px;
  }
  100% {
    top: 0px;
  }
}

@keyframes compare-overlay-slide-open {
  0% {
    top: -2000px;
  }
  100% {
    top: 0px;
  }
}

.compare-outer-container  {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0px;
  left: 0px;
  -webkit-animation-name: compare-overlay-slide-open;
          animation-name: compare-overlay-slide-open;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  z-index: 1000;
}

.compare-container  {
  width: 300px;
  height: 380px;
  background: #dce4e7;
  box-shadow: 0px 0px 30px black;
  border-radius: 10px;
  -webkit-animation-name: compare-overlay-slide-open;
          animation-name: compare-overlay-slide-open;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

.compared-title {
  font-size: 20px;
  color: #FFF;
  background: #1b1f2a;
  text-align: center;
  padding: 20px 0 10px 0;
  margin-bottom: 20px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.district1  {
  font-size: 15px;
  color: #1b1f2a;
  text-align: center;
  padding: 10px 0 10px 0;
}

.compared  {
  font-size: 15px;
  color: #1b1f2a;
  text-align: center;
  padding: 10px 0 10px 0;
}

.district2  {
  font-size: 15px;
  color: #1b1f2a;
  text-align: center;
  padding: 10px 0 10px 0;
}

.close-button {
  width: 100px;
  height: 40px;
  margin: auto;
  margin-top: 24px;
  background: #1b1f2a;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 12px black;
  border-radius: 10px;
}

.close-button:hover {
  cursor: pointer;
  background: #619ddb;
}

.close-button-title {
  font-size: 14px;
  color: #FFF;
}

